var HpMpTable = require('./constants/heroHPMPConstants.json')

const heroLimitHpMp = () => {

  const getHpMp = (character) => {

    if(character.name == "Bell Cranel"){

      return HpMpTable.bal

    }else{
      switch(character.type_attack){
        case "physical_type":
          return HpMpTable.phys
        case "magic_type":
          return HpMpTable.mag
        case "balance_type":
          return HpMpTable.bal
        case "balance_type_old":
          return HpMpTable.bal
        case "bala_phys":
          return HpMpTable.bal
        case "bala_mag":
          return HpMpTable.bal
        case "defense_type":
          return HpMpTable.def
        case "healer_type":
          return HpMpTable.heal
        case "phys_plus":
          return HpMpTable.phys
        case "mag_plus":
          return HpMpTable.mag
        case "bala_plus_phys":
          return HpMpTable.phys
        case "bala_plus_mag":
          return HpMpTable.mag
        case "phys_festa":
          return HpMpTable.phys
        default:
          return HpMpTable.empty
      }
    }
  }

  return { getHpMp }
}

export default heroLimitHpMp