var limitBreakTable = require('./constants/heroLimitConstants.json')

const heroLimitStats = () => {

  const getStats = (character) => {

    if(character.name != "Bell Cranel"){

      return getChar(character)

    }else{
      switch(character.title){
        case "Argonaut":
          return limitBreakTable.normal.bell.bell_argonaut
        case "Ox Slayer":
          return limitBreakTable.normal.bell.bell_oxslayer
        case "Little Freese":
          return limitBreakTable.normal.bell.bell_littlefreese
        case "Liaris Freese":
          return limitBreakTable.normal.bell.bell_liaris
        case "Adventurer":
          return limitBreakTable.normal.bell.bell_adventurer
        default:
          return getChar(character)

      }
    }
  }

  const getChar = (character) => {
    const map4 = {
      // 4 Stars
      "physical_type": limitBreakTable.normal.main['4stars'].physical,
      "magic_type": limitBreakTable.normal.main['4stars'].magic,
      "bala_phys": limitBreakTable.normal.main['4stars'].balanced,
      "bala_mag": limitBreakTable.normal.main['4stars'].balanced,
      "balance_type": limitBreakTable.normal.main['4stars'].balanced,
      "balance_type_old": limitBreakTable.normal.main['4stars'].balanced_old,
      "defense_type": limitBreakTable.normal.main['4stars'].defense,
      "healer_type": limitBreakTable.normal.main['4stars'].healer,

      // PLUS
      "phys_plus": limitBreakTable.plus.main.physical,
      "mag_plus": limitBreakTable.plus.main.magic,
      "bala_plus_phys": limitBreakTable.plus.main.balanced_str,
      "bala_plus_mag": limitBreakTable.plus.main.balanced_mag,

      // Festa
      "phys_festa": limitBreakTable.festa.main.str
    }
    const map4free = {
      // 4 Stars Free
      "physical_type": limitBreakTable.normal.free['4stars'].physical,
      "magic_type": limitBreakTable.normal.free['4stars'].magic,
      "bala_phys": limitBreakTable.normal.free['4stars'].balanced,
      "bala_mag": limitBreakTable.normal.free['4stars'].balanced,
      "balance_type": limitBreakTable.normal.free['4stars'].balanced,

      // PLUS
      "phys_plus": limitBreakTable.plus.free.physical,
    }
    const map3 = {
      // 3 Stars
      "physical_type": limitBreakTable.normal.main['3stars'].physical,
      "magic_type": limitBreakTable.normal.main['3stars'].magic,
      "balance_type": limitBreakTable.normal.main['3stars'].balanced,
      "defense_type": limitBreakTable.normal.main['3stars'].defense,
      "healer_type": limitBreakTable.normal.main['3stars'].healer,
    }
    const map3free = {
      // 3 Stars Free
      "physical_type": limitBreakTable.normal.free['3stars'].physical,
      "magic_type": limitBreakTable.normal.free['3stars'].magic,
      "balance_type": limitBreakTable.normal.free['3stars'].balanced,
      "healer_type": limitBreakTable.normal.free['3stars'].healer,
    }
    const map2 = {
      // 2 Stars
      "physical_type": limitBreakTable.normal.main['2stars'].physical,
      "magic_type": limitBreakTable.normal.main['2stars'].magic,
      "balance_type": limitBreakTable.normal.main['2stars'].balanced,
      "defense_type": limitBreakTable.normal.main['2stars'].defense,
      "healer_type": limitBreakTable.normal.main['2stars'].healer,
    }

    let res

    if(character.is_free){
      switch(character.stars){
        case 4:
          res = map4free[character.type_attack] ?? []
          break
        case 3:
          res = map3free[character.type_attack] ?? []
          break
        default:
          res = limitBreakTable.empty
          break
      }
    }else{
      switch(character.stars){
        case 4:
          res = map4[character.type_attack] ?? []
          break
        case 3:
          res = map3[character.type_attack] ?? []
          break
        case 2:
          res = map2[character.type_attack] ?? []
          break
        default:
          res = limitBreakTable.empty
          break
      }
    }

    return res
    
  }

  const calculateStats = (limitValues, devSkills) => {

    let statsIncrease = {
      "Strength": 1,
      "Endurance": 1,
      "Dexterity": 1,
      "Agility": 1,
      "Magic": 1,
    }

    for(let i = 0; i < 5; i++){
      for(let j = 0; j < devSkills[i].n_efeitos; j++){

        let changeValue = parseFloat(devSkills[i]["value_" + (j+1)])/100

        switch(devSkills[i]["effect_" + (j+1)]){
          case "Strength": 
            statsIncrease.Strength += changeValue
            break;
          case "Endurance": 
            statsIncrease.Endurance += changeValue
            break;
          case "Dexterity": 
            statsIncrease.Dexterity += changeValue
            break;
          case "Agility": 
            statsIncrease.Agility += changeValue
            break;
          case "Magic": 
            statsIncrease.Magic += changeValue
            break;
          case "All Status": 
            statsIncrease.Strength += changeValue
            statsIncrease.Endurance += changeValue
            statsIncrease.Dexterity += changeValue
            statsIncrease.Agility += changeValue
            statsIncrease.Magic += changeValue
            break;
        }
      }
    }

    let newValues = [...limitValues]

    newValues[0] = Math.round(limitValues[0] * statsIncrease.Strength)
    newValues[1] = Math.round(limitValues[1] * statsIncrease.Endurance)
    newValues[2] = Math.round(limitValues[2] * statsIncrease.Dexterity)
    newValues[3] = Math.round(limitValues[3] * statsIncrease.Agility)
    newValues[4] = Math.round(limitValues[4] * statsIncrease.Magic)
    return newValues

  }

  return { getStats, calculateStats }
}

export default heroLimitStats