var heroAscension = require('./constants/heroAscentConstants.json')

const heroAscentStats = () => {

  const getAscension = (character) => {
    if(!character.have_hero_asc){
      return heroAscension.empty
    }

    if(character.is_free){
      switch(character.type_attack){
        case "physical_type":
          return heroAscension.normal.free.phys
        case "magic_type":
          return heroAscension.normal.free.mag
        case "bala_phys":
          return heroAscension.normal.free.balanced_phys
        case "bala_mag":
          return heroAscension.normal.free.balanced_mag
        case "phys_plus":
          return heroAscension.plus.free.phys
        default:
          return heroAscension.empty
      }
    }else{
      switch(character.type_attack){
        case "physical_type":
          return heroAscension.normal.main.phys
        case "magic_type":
          return heroAscension.normal.main.mag
        case "bala_phys":
          return heroAscension.normal.main.balanced_phys
        case "bala_mag":
          return heroAscension.normal.main.balanced_mag
        case "defense_type":
          return heroAscension.normal.main.def
        case "healer_type":
          return heroAscension.normal.main.healer
        case "phys_plus":
          return heroAscension.plus.main.phys
        case "mag_plus":
          return heroAscension.plus.main.mag
        case "bala_plus_phys":
          return heroAscension.plus.main.balanced_phys
        case "bala_plus_mag":
          return heroAscension.plus.main.balanced_mag
        case "phys_festa":
          return heroAscension.plus.main.phys
        default:
          return heroAscension.empty
      }
    }
  }

  return { getAscension }
}

export default heroAscentStats