<template>
  <div class="main_char">
    <div id="backButton" @click="gotoChar()">
    </div>
    <div class="char_page" :style="screenWidth >= 768 ? {width: '75%', minWidth: '640px', maxWidth: '60%'} : {width: '95%', minWidth: '320px', maxWidth: '640px'}">
      <!-- <div id="arrowCharStyle_adview">
        <img alt="Go Back" id="arrowImage_adview" src="../assets/left_arrow.png" />
      </div> -->
      <div id="adventureTagWrapper">
        <img alt="Adventurer" id="adventureTag" src="../assets/adv_banner.png" />
      </div>
      <div v-if="error" id="errorPag">
        <n-result status="error" title="Error">
          <p>{{ errorMessage }}</p>
        </n-result>
      </div>
      <div v-if="character" class="info_page">
        <div class="adventureInfo">
          <n-space vertical>
            <n-space vertical id=adventureInfo_title>
              <span>[{{character.title}}]</span>
              <span>{{ character.name }}</span>
            </n-space>
            <div class="adv_image" @click="showModel = true">
              <img alt="Frame" :src="decideBase(character.stars, character.have_hero_asc, character.hero_festa)">
              <!-- <img :alt="character.id_intern" :src="getImg(character.id_intern)"> -->
              <img alt="Frame_front" :src="decideBaseFrame(character.stars, character.have_hero_asc, character.hero_festa)">
            </div>
          </n-space>
          <n-button id="reportProblem" type=error @click="gotoReport">Report Problem</n-button>
        </div>
        <div class="char_wrapper">
          <div class="char_image" v-if="screenWidth >= 768">
            <!-- <img id="char_rectangle" alt="CharRectangle" :src="getRec(character.id_intern)" @click="showModel = true" /> -->
          </div>
          <n-modal v-model:show="showModel">
            <n-card
              style="width: auto; height: 50%; position: relative; justify-content: center;"
              :bordered="false"
              size="huge"
              role="dialog"
              aria-modal="true"
            >
              <n-image
                :src="getModel(character.id_intern)"
                width="400"
                id="char_model"
              >
              </n-image>
              <p id="char_model_text">Click on the image to zoom</p>
              <n-button id="char_model_closebutton" type="error" @click="showModel = false">
                Close
              </n-button>
            </n-card>
          </n-modal>
          <div class="information_section" :style="screenWidth >= 768 ? {borderLeftWidth: '1px', width: '75%'} : {borderLeftWidth: '0px', width: '100%'}">
            <n-space vertical>
              <div class="attr_level_section" :style="screenWidth >= 768 ? {flexDirection: 'row', paddingLeft: '20px'} : {flexDirection: 'column',  paddingLeft: '0px'}">
                <div id="attrInfoPage" :style="screenWidth >= 768 ? {width: '36%'} : {width: '96%'}">
                  <n-space vertical>
                    <div id="typeWrapperType">
                      <img :alt="character.type_attack" :src="decideType(character.type_attack)" width="25" height="25"/>
                      <span>{{ decideTypePhys(character.type_attack) }}</span>
                    </div>
                    <div id="typeWrapperElem">
                      <img :alt="character.type_element" :src="decideElement(character.type_element)" width="21" height="21" />
                      <span>{{ decideElementLabel(character.type_element) }} Type</span>
                    </div>
                    <div id="typeWrapperCard" v-if="character.time_limited">
                      <!-- <span>Time limited: {{ decideTrueFalse(character.time_limited) }}</span> -->
                      <img :alt="character.time_limited" src="../assets/attacktype/timelimited.png" />
                    </div>
                    <div id="typeWrapperCard" v-if="character.is_free">
                      <!-- <span>Free Unit: {{ decideTrueFalse(character.is_free) }}</span> -->
                      <img :alt="character.is_free" src="../assets/attacktype/freeunit.png" />
                    </div>
                    <div id="typeWrapperCard" v-if="character.hero_festa">
                      <!-- <span>Free Unit: {{ decideTrueFalse(character.is_free) }}</span> -->
                      <img :alt="character.is_free" src="../assets/attacktype/herofesta.png" />
                    </div>
                    <div id="typeWrapperCard" v-if="character.killer != 'none'">
                      <span>{{ decideTypeKiller(character.killer) }}</span>
                    </div>
                    
                  </n-space>
                </div>
                <!-- <div id="levelPage"> -->
                <n-space vertical id="levelPage" :style="screenWidth >= 768 ? {width: '60%', margin: '0px 0px 0px'} : {width: '100%', margin: '40px 0px 0px'}">
                  <div id="minmaxStats">
                    <n-button type="default" id="minmaxButtons" @click="changeMaxMin(true)">
                      Min
                    </n-button>
                    <n-button type="default" id="minmaxButtons" @click="changeMaxMin(false)">
                      Max
                    </n-button>
                  </div>
                  <!-- :style="screenWidth >= 768 ? {width: '220px'} : {width: '135px'}" -->
                  <div id="heroascentSection" v-if="character.have_hero_asc">
                    <n-space vertical>
                      <span v-if="screenWidth >= 768">Hero Ascension Level</span>
                      <span v-else>Ascension Level</span>
                      <div id="heroascentCounter">
                        <n-button @click="changeAscent(-1)">-</n-button>
                        <div id="starGroup" @click="clickAscent" ref="ascentStars">
                          <div id="star_border" v-for="i in 6" 
                            :style="{
                              left: (14 + (21*(i-1))).toString() + 'px',
                              background: ascent >= i ? '#84757F' : '#966f50'
                            }">
                            <div id="star_intern" 
                              :style="{
                                background: ascent >= i ? 'radial-gradient(circle, rgba(255,208,255,1) 0%, rgba(255,139,255,1) 31%, rgba(213,60,210,1) 70%, rgba(167,32,167,1) 100%)' : '#ffe159',
                                backgroundPosition: ascent >= i ? 'right 2px bottom 2px' : 'right 0px bottom 0px'
                              }"
                            >
                            </div>
                          </div>
                        </div>
                        <!-- <img id="counterImage" :alt="character.id_intern" :src="decideAscent()" @click="clickAscent" ref="ascentStars" /> -->
                        <n-button @click="changeAscent(1)">+</n-button>
                      </div>
                    </n-space>
                  </div>
                  <!-- :style="screenWidth >= 768 ? {width: '220px'} : {width: '135px'}" -->
                  <n-space vertical id="limitbreakSection">
                    <span>Limit Break Level</span>
                    <div id="limitbreakCounter">
                      <n-button @click="changeLimit(-1)">-</n-button>
                      <div id="limitGroup" @click="clickLimit" ref="limitLevel">
                        <div id="limitDiamond" v-for="i in 5"
                          :style="{
                            left: (9 + (28*(i-1))).toString() + 'px',
                            background: limitbreak >= i ? 
                            'linear-gradient(135deg, rgba(255,123,138,1) 0%, rgba(236,57,75,1) 38%, rgba(255,177,187,1) 100%)' :
                            'linear-gradient(135deg, rgba(176,175,171,1) 0%, rgba(198,197,193,1) 100%)',
                            border: limitbreak >= i ?
                            '#683633 1px solid' :
                            '#9da1a6 1px solid'
                            // background: ascent >= i ? '#84757F' : '#966f50'
                          }"
                        >
                        </div>
                      </div>
                      <!-- <img id="counterImage" :alt="character.id_intern" :src="decideLimit()" @click="clickLimit" ref="limitLevel" /> -->
                      <n-button @click="changeLimit(1)" >+</n-button>
                    </div>
                  </n-space>
                </n-space>
              </div>
              <n-space vertical class="falna_window">
                <n-space vertical class="statusPage">
                  <div id="statusBar">
                    <img :alt="character.id_intern" src="../assets/status/hpbar.png" />
                    <div id="status_text">
                      <span id="mainStat" :style="screenWidth >= 768 ? {right: '130px'} : {right: '10px'}">{{ character.hp + ascentValue[5] + limitbreakValueHpMp[0] }}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakValueHpMp[0] }}</span>
                      <span id="bonusAscentStat" v-if="screenWidth >= 768"> + {{ ascentValue[5] }}</span>
                    </div>
                  </div>
                  <div id="statusBar">
                    <img :alt="character.id_intern" src="../assets/status/mpbar.png" />
                    <div id="status_text">
                      <span id="mainStat" :style="screenWidth >= 768 ? {right: '130px'} : {right: '10px'}">{{ character.mp + ascentValue[6] + limitbreakValueHpMp[1]}}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakValueHpMp[1] }}</span>
                      <span id="bonusAscentStat" v-if="screenWidth >= 768"> + {{ ascentValue[6] }}</span>
                    </div>
                  </div>
                  <div id="statusBar">
                    <img :alt="character.id_intern" src="../assets/status/atkbar.png" />
                    <div id="status_text">
                      <span id="mainStat" :style="screenWidth >= 768 ? {right: '130px'} : {right: '10px'}">{{ character.str + ascentValue[0] + limitbreakDisplay[0] }}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakDisplay[0] }}</span>
                      <span id="bonusAscentStat" v-if="screenWidth >= 768"> + {{ ascentValue[0] }}</span>
                    </div>
                  </div>
                  <div id="statusBar">
                    <img :alt="character.id_intern" src="../assets/status/magbar.png" />
                    <div id="status_text">
                      <span id="mainStat" :style="screenWidth >= 768 ? {right: '130px'} : {right: '10px'}">{{ character.mag + ascentValue[4] + limitbreakDisplay[4] }}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakDisplay[4] }}</span>
                      <span id="bonusAscentStat" v-if="screenWidth >= 768"> + {{ ascentValue[4] }}</span>
                    </div>
                  </div>
                  <div id="statusBar">
                    <img :alt="character.id_intern" src="../assets/status/defbar.png" />
                    <div id="status_text">
                      <span id="mainStat" :style="screenWidth >= 768 ? {right: '130px'} : {right: '10px'}">{{ character.end + ascentValue[1] + limitbreakDisplay[1] }}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakDisplay[1] }}</span>
                      <span id="bonusAscentStat" v-if="screenWidth >= 768"> + {{ ascentValue[1] }}</span>
                    </div>
                  </div>
                </n-space>
                <n-space vertical class="falnaPage">
                  <div id="falnaBar">
                    <div id="falna_text">
                      <span id="mainFalnaLabel">Str.</span>
                      <div  id="falnaStatus">
                        <img :alt="falnaImages[0]" :src="getFalnaImages(falnaImages[0])" />
                      </div>
                      <span id="mainFalnaValue" :style="screenWidth >= 768 ? {right: '130px'} : {right: '10px'}">{{ character.str + ascentValue[0] + limitbreakDisplay[0] }}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakDisplay[0] }}</span>
                      <span id="bonusAscentStat" v-if="screenWidth >= 768"> + {{ ascentValue[0] }}</span>
                    </div>
                  </div>
                  <div id="falnaBar">
                    <div id="falna_text">
                      <span id="mainFalnaLabel">End.</span>
                      <div  id="falnaStatus">
                        <img :alt="falnaImages[1]" :src="getFalnaImages(falnaImages[1])" />
                      </div>
                      <span id="mainFalnaValue" :style="screenWidth >= 768 ? {right: '130px'} : {right: '10px'}">{{ character.end + ascentValue[1] + limitbreakDisplay[1]}}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakDisplay[1] }}</span>
                      <span id="bonusAscentStat" v-if="screenWidth >= 768"> + {{ ascentValue[1] }}</span>
                    </div>
                  </div>
                  <div id="falnaBar">
                    <div id="falna_text">
                      <span id="mainFalnaLabel">Dex.</span>
                      <div  id="falnaStatus">
                        <img :alt="falnaImages[2]" :src="getFalnaImages(falnaImages[2])" />
                      </div>
                      <span id="mainFalnaValue" :style="screenWidth >= 768 ? {right: '130px'} : {right: '10px'}">{{ character.dex + ascentValue[2] + limitbreakDisplay[2]}}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakDisplay[2] }}</span>
                      <span id="bonusAscentStat" v-if="screenWidth >= 768"> + {{ ascentValue[2] }}</span>
                    </div>
                  </div>
                  <div id="falnaBar">
                    <div id="falna_text">
                      <span id="mainFalnaLabel">Agi.</span>
                      <div  id="falnaStatus">
                        <img :alt="falnaImages[3]" :src="getFalnaImages(falnaImages[3])" />
                      </div>
                      <span id="mainFalnaValue" :style="screenWidth >= 768 ? {right: '130px'} : {right: '10px'}">{{ character.agi + ascentValue[3] + limitbreakDisplay[3]}}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakDisplay[3] }}</span>
                      <span id="bonusAscentStat" v-if="screenWidth >= 768"> + {{ ascentValue[3] }}</span>
                    </div>
                  </div>
                  <div id="falnaBar">
                    <div id="falna_text">
                      <span id="mainFalnaLabel">Mag.</span>
                      <div id="falnaStatus">
                        <img :alt="falnaImages[4]" :src="getFalnaImages(falnaImages[4])" />
                      </div>
                      <span id="mainFalnaValue" :style="screenWidth >= 768 ? {right: '130px'} : {right: '10px'}">{{ character.mag + ascentValue[4] + limitbreakDisplay[4] }}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakDisplay[4] }}</span>
                      <span id="bonusAscentStat" v-if="screenWidth >= 768"> + {{ ascentValue[4] }}</span>
                    </div>
                  </div>
                </n-space>
              </n-space>
            </n-space>
          </div>
        </div>
        <n-space vertical id="skillSection">
          <div id="attack_wrapper">
            <span>Special/Combat Skills</span>
            <div id="attack_section">
              <n-card id="combat_cards" v-for="(value, key, index) in character.combat_skills" v-bind:key="index" @click="colapseCombat(index)"
                :style="festaSkill[index] ? { backgroundColor: '#f9f8e8' } : { backgroundColor: '#ffffff' }">
                <div id="combat_card_title">
                  <div id="combat_skill_element">
                    <span>
                      {{ decideSkillsLabel(key) }}
                    </span>
                    <img alt="element" :src="decideAttackElement(value)" width="21" heigth="21" />
                  </div>
                  <div id="combat_card_skill_name">
                    <span>
                      {{ value.name }}
                    </span>
                  </div>
                  <span id="combat_mp" v-if="value.mp_used">
                    MP {{ value.mp_used }}
                  </span>
                  <div id="combat_card_festa_wrapper" v-if="value.heroFesta">
                    <div 
                      title="Toggle Attack Festa Skill"
                      id="festa_diamond" 
                      :style="festaSkill[index] ? {
                        background: 'linear-gradient(135deg, rgba(255,123,138,1) 0%, rgba(236,57,75,1) 38%, rgba(255,177,187,1) 100%)',
                        border: '#683633 1px solid'
                      } : {
                        background: 'linear-gradient(135deg, rgba(176,175,171,1) 0%, rgba(198,197,193,1) 100%)',
                        border: '#9da1a6 1px solid'
                      }"
                      @click.stop="swapAttack(index)">
                      </div>
                  </div>
                </div>
                <div id="combat_card_raw" >{{ value.raw_text }}</div>
                <div id="combat_card_arrow" 
                    :style="{
                      clipPath: combat_dropdown[index] ? 'polygon(50% 100%, 0 0, 100% 0)' : 'polygon(50% 0, 0 100%, 100% 100%)'
                    }"
                  >
                </div>
                <Transition name="combat_skill">
                  <div id="combat_card_modal" v-if="combat_dropdown[index]">
                    <div id="combat_card_attacks" v-if="Object.keys(value.attack).length !== 0 || Object.keys(value.repeat_attack).length !== 0">
                      <n-card id="combat_card_attack_box_unit" :style="festaSkill[index] ? { backgroundColor: '#f9f8e8', border: 'rgba(128, 128, 128, 0.6) 1px solid'  } : { backgroundColor: '#ffffff' }">
                        <n-divider title-placement="Center" v-if="Object.keys(value.attack).length !== 0">
                          Attack
                        </n-divider>
                        <div id="combat_card_combat_main" :style="screenWidth >= 768 ? {flexDirection: 'row'} : {flexDirection: 'column'}" v-if="Object.keys(value.attack).length !== 0">
                          <n-card size="small" id="combat_card_combat_main_individual" :style="screenWidth >= 768 ? {width: 'fit-content'} : {width: '100%'}" @click.stop="goAttack(value)">
                            <span>[{{ value.attack.attack_target }}]&nbsp;</span>
                            <span v-if="value.attack.attack_velocity != 'none'">{{ value.attack.attack_velocity }}&nbsp;</span>
                            <span>{{ value.attack.attack_power }}&nbsp;</span>
                            <span v-if="value.attack.attack_element != 'none'">{{ value.attack.attack_element }}&nbsp;</span>
                            <span>{{ value.attack.attack_type }}&nbsp;</span>
                          </n-card>
                        </div>
                        <div id="combat_card_combat_main" style="flex-direction: column">
                          <n-card size="small" id="combat_card_combat_main_individual" v-if="value.attack.has_attack_mod" :style="screenWidth >= 768 ? {width: 'fit-content'} : {width: '100%'}"
                            @click.stop="goModifier(value)">
                            <span> w/ {{ value.attack.attack_modifier1 }}</span>
                          </n-card>
  
                          <n-card size="small" id="combat_card_combat_main_individual" v-if="value.attack.has_index" :style="screenWidth >= 768 ? {width: 'fit-content'} : {width: '100%'}"
                            @click.stop="goIndex(value, 1)">
                            Indexed to <span>{{ value.attack.index_1 }}</span>
                          </n-card>

                          <n-card size="small" id="combat_card_combat_main_individual" v-if="value.attack.has_index && value.attack.index_2" :style="screenWidth >= 768 ? {width: 'fit-content'} : {width: '100%'}"
                            @click.stop="goIndex(value, 2)">
                            and Indexed to <span>{{ value.attack.index_2 }}</span>
                          </n-card>
                        </div>
    
                        <div id="combat_card_attack_effects" v-if="checkEachAttack(value)">
                          <n-divider title-placement="Center">
                            Modifications
                          </n-divider>
                          <span>+{{ value.attack.perEachNumber }} per each [</span>
                          <span style="font-weight: bold; ">{{ value.attack.perEachTarget }}]</span>
                          <n-card size="small" id="combat_card_attack_pereach" v-for="eff in value.attack.perEachEffect_n" :key="eff"
                            :style="{
                              width: (screenWidth >= 768 ? 'fit-content' : '100%'),
                              backgroundColor: (perEachNegative(value, eff) ? '#FFFAF9' : '#EFFFF1')
                            }" 
                            @click.stop="goPerEach(value, eff)">
                            <span>
                              {{ value.attack["perEachEffect" + eff] }}
                            </span>
                          </n-card>
                        </div>
                        <div id="combat_card_attack_effects" v-if="value.hasRepeat">
                          <n-divider title-placement="Center">
                            Repeat Attack
                          </n-divider>
                          <div v-if="value.repeat_attack.repeat_has_attack">
                            <div id="combat_card_combat_main">
                              <n-card size="small" id="combat_card_combat_main_individual" :style="screenWidth >= 768 ? {width: 'fit-content'} : {width: '100%'}"
                                @click.stop="goRepeat(value)">
                                <span>+{{ value.repeat_attack.repeatCount }} Actions&nbsp;</span><br/>
                              </n-card>
                            </div>
                            <div id="combat_card_combat_main" :style="screenWidth >= 768 ? {flexDirection: 'row'} : {flexDirection: 'column'}">
                              <n-card size="small" id="combat_card_combat_main_individual" :style="screenWidth >= 768 ? {width: 'fit-content'} : {width: '100%'}"
                                @click.stop="goRepeatAttack(value)">
                                <span>[{{ value.repeat_attack.attack_target }}]&nbsp;</span>
                                <span v-if="value.repeat_attack.attack_velocity != 'none'">{{ value.repeat_attack.attack_velocity }}&nbsp;</span>
                                <span>{{ value.repeat_attack.attack_power }}&nbsp;</span>
                                <span v-if="value.repeat_attack.attack_element != 'none'">{{ value.repeat_attack.attack_element }}&nbsp;</span>
                                <span>{{ value.repeat_attack.attack_type }}&nbsp;</span>
                              </n-card>
                            </div>
                            <div id="combat_card_combat_main" style="flex-direction: column">
                              <n-card size="small" id="combat_card_combat_main_individual" v-if="value.repeat_attack.has_attack_mod" :style="screenWidth >= 768 ? {width: 'fit-content'} : {width: '100%'}"
                                @click.stop="goModifierRepeat(value)">
                                <span> w/ {{ value.repeat_attack.attack_modifier1 }}</span>
                              </n-card>
                              <n-card size="small" id="combat_card_combat_main_individual" v-if="value.repeat_attack.has_index" :style="screenWidth >= 768 ? {width: 'fit-content'} : {width: '100%'}"
                                @click.stop="goIndexRepeat(value, 1)">
                                Indexed to <span>{{ value.repeat_attack.index_1 }}</span>
                              </n-card>
                              <n-card size="small" id="combat_card_combat_main_individual" v-if="value.repeat_attack.has_index && value.repeat_attack.index_2" :style="screenWidth >= 768 ? {width: 'fit-content'} : {width: '100%'}"
                                @click.stop="goIndexRepeat(value, 2)">
                                and Indexed to <span>{{ value.repeat_attack.index_2 }}</span>
                              </n-card>
                            </div>
                            <br />
                            <div id="combat_card_attack_effects" v-if="checkEachAttackRepeat(value)">
                              <span>+{{ value.repeat_attack.perEachNumber }} per each [</span>
                              <span style="font-weight: bold; ">{{ value.repeat_attack.perEachTarget }}]</span>
                              <n-card id="combat_card_attack_pereach" v-for="eff in value.repeat_attack.perEachEffect_n" :key="eff" size="small"
                                :style="{
                                  width: (screenWidth >= 768 ? 'fit-content' : '100%'),
                                  backgroundColor: (perEachNegative(value, eff) ? '#FFFAF9' : '#EFFFF1')
                                }"  
                                @click.stop="goPerEachRepeat(value, eff)">
                                <span>
                                  {{ value.repeat_attack["perEachEffect" + eff] }}
                                </span>
                              </n-card>
                            </div>
                          </div>
                          <div v-if="value.repeat_attack.effects" style="margin-top: 24px">
                            <n-divider title-placement="Center">
                              Repeat Effects
                            </n-divider>
                            <n-card size="small" id="combat_card_attack_box_effect" v-for="combat_skill in value.repeat_attack.effects.n_effects" :key="combat_skill"
                              @click.stop="goEffectRepeat(value, combat_skill)">
                              <span>[{{ value.repeat_attack.effects[combat_skill + "targets"]}}]&nbsp;</span>
                              <span>{{ value.repeat_attack.effects[combat_skill + "effect"]}}&nbsp;</span>
                              <span v-if="value.repeat_attack.effects[combat_skill + 'value']">{{ value.repeat_attack.effects[combat_skill + "value"]}}&nbsp;</span>
                              <span v-if="value.repeat_attack.effects[combat_skill + 'turns']">/ {{ value.repeat_attack.effects[combat_skill + "turns"]}} turn(s)&nbsp;</span>
                            </n-card>
                          </div>
                        </div>
                      </n-card>
                    </div>
                    <div id="combat_card_attacks" v-if="Object.keys(value.effects).length !== 0">
                      <n-card title="Effects" id="combat_card_attack_box" :style="festaSkill[index] ? { backgroundColor: '#f9f8e8', border: 'rgba(128, 128, 128, 0.6) 1px solid'  } : { backgroundColor: '#ffffff' }">
                        <!-- <n-divider title-placement="Center">Effects</n-divider> -->
                        <n-card size="small" id="combat_card_attack_box_effect" v-for="combat_skill in value.effects.n_effects" :key="combat_skill"
                          @click.stop="goEffect(value, combat_skill)" 
                          :style="{
                            backgroundColor: (negativeEff(value, combat_skill) ? '#FFFAF9' : '#EFFFF1')
                          }"
                          >
                          <span>[{{ value.effects[combat_skill + "targets"]}}]&nbsp;</span>
                          <span>{{ value.effects[combat_skill + "effect"]}}&nbsp;</span>
                          <span v-if="value.effects[combat_skill + 'value']">{{ value.effects[combat_skill + "value"]}}&nbsp;</span>
                          <span v-if="value.effects[combat_skill + 'turns']">/ {{ value.effects[combat_skill + "turns"]}} turn(s)&nbsp;</span>
                        </n-card>
                      </n-card>
                    </div>
                  </div>
                </Transition>
              </n-card>
            </div>
          </div>
          <div id="combination_wrapper" v-if="character.combination_attack.has_combination">
            <span>Combination Attack</span>
            <div id="combination_section" v-if="screenWidth >= 768" :title="'Go to Filter Combination Attack'">
              <n-card v-for="comb_skill in character.combination_attack.n_combination" :key="comb_skill" id="comb_cards" @click="goCombination(character.combination_attack['combination_attack_' + comb_skill])">
                {{ character.combination_attack["combination_attack_" + comb_skill] }}
              </n-card>
            </div>
            <n-space vertical id="combination_section" v-else :title="'Go to Filter Combination Attack'">
              <n-card v-for="comb_skill in character.combination_attack.n_combination" :key="comb_skill" id="comb_cards_vertical" @click="goCombination(character.combination_attack['combination_attack_' + comb_skill])">
                {{ character.combination_attack["combination_attack_" + comb_skill] }}
              </n-card>
            </n-space>
          </div>

          <div id="passive_wrapper">
            <span>Skills/Development Abilities</span>
            <div id="passive_section">
              <n-card id="passive_card" v-for="i in character.dev_abilities.length" :key="i" 
                :style="character.hero_festa && (i-1) > 4 ? { backgroundColor: '#f9f8e8', border: 'rgba(128, 128, 128, 0.6) 1px solid'} : { backgroundColor: '#ffffff' }">
                <div id="passive_card_title" :title="'Go to Filter: ' + character.dev_abilities[i-1].name" 
                  v-if="character.dev_abilities[i-1].level == '' || character.dev_abilities[i-1].level == 'X'">
                  <n-card size="small" id="passive_card_name" @click="goDevelopmentName(character.dev_abilities[i-1])"
                    :style="character.hero_festa && (i-1) > 4 ? { backgroundColor: '#f9f8e8', borderColor: '#f9f8e8' } : { backgroundColor: '#ffffff' }">
                    {{ character.dev_abilities[i-1].name }}
                  </n-card>
                </div>
                <div id="passive_card_title" :title="'Go to Filter: ' + character.dev_abilities[i-1].name" v-else>
                  <n-card size="small" id="passive_card_name" @click="goDevelopmentName(character.dev_abilities[i-1])"
                    :style="character.hero_festa && (i-1) > 4 ? { backgroundColor: '#f9f8e8', borderColor: '#f9f8e8'  } : { backgroundColor: '#ffffff' }">
                    {{ character.dev_abilities[i-1].name }}: {{ character.dev_abilities[i-1].level }}
                  </n-card>
                </div>
                <div id="passive_festa_indicator" v-if="character.hero_festa && (i-1) > 4">
                  <img :alt="'Festa'" :src="getFesta(i-1)" title="Hero Festa Unlockable Development Skills">
                </div>
                <div id="passive_card_effects" >
                  <span v-for="dev in character.dev_abilities[i-1].n_efeitos" :key="dev" :title="'Go to Filter: ' + character.dev_abilities[i-1]['effect_' + dev]">
                    <div id="passive_card_main" style="flexDirection: row">
                      <n-card size="small" id="passive_card_individual" @click="goDevelopment(character.dev_abilities[i-1], dev)">
                        <span>{{ character.dev_abilities[i-1]["effect_" + dev] }}</span>
                        <span v-if="character.dev_abilities[i-1]['value_' + dev] != ''" id="passive_text_color1">&nbsp;{{ character.dev_abilities[i-1]["value_" + dev] }}</span>
                        <span v-if="character.dev_abilities[i-1]['turns_' + dev] != ''"> / </span>
                        <span v-if="character.dev_abilities[i-1]['turns_' + dev] != ''" id="passive_text_color2">{{ character.dev_abilities[i-1]["turns_" + dev] }} turns</span>
                        <span v-if="character.dev_abilities[i-1]['who_' + dev] != ''" id="passive_text_color3"> on {{ character.dev_abilities[i-1]["who_" + dev] }}</span>
                        <span v-if="character.dev_abilities[i-1]['how_' + dev] != ''" id="passive_text_color4"> on {{ character.dev_abilities[i-1]["how_" + dev] }}</span>
                      </n-card>
                    </div>
                  </span>
                </div>
              </n-card>
            </div>
          </div>
          <div id="attackDiv">

          </div>
        </n-space>
      </div>
    </div>
  </div>
</template>

<script>
import {  
  NButton,
  NSpace,
  NSelect,
  NResult,
  NModal,
  NImage,
  NCard,
  NDivider,
  NCollapse,
  NCollapseItem,
} from 'naive-ui'
import axios from 'axios'
import heroAscentStats from '@/composables/heroAscentStats'
import numberToRank from '@/composables/numberToRank'
import heroLimitStats from '@/composables/heroLimitStats'
import heroLimitHpMp from '@/composables/heroLimitHpMp'
import { ref } from '@vue/reactivity'

export default {
  props: ["id"],
  data() {
    return {
      id_char: this.id,
      character: null,
      error: null,
      errorMessage: "",
      limitbreak: 0,
      ascent: 0,
      ascentStats: null,
      limitbreakStats: null,
      limitbreakHpMp: null,

      negativeEffects: null,

      festaSkill: [false, false, false, false],

      showModel: ref(false),
      screenWidth: window.innerWidth,
      // STR, END, DEX, AGI, MAG, HP, MP
      ascentValue: [0, 0, 0, 0, 0, 0, 0],
      limitbreakValue: [0, 0, 0, 0, 0],
      limitbreakDisplay: [0, 0, 0, 0, 0],
      limitbreakValueHpMp: [0, 0],

      combat_dropdown: [false, false, false, false],

      falnaImages: ["", "", "", "", ""]
    }
  },
  components:{
    NButton,
    NSpace,
    NSelect,
    NResult,
    NModal,
    NImage,
    NCard,
    NDivider,
    NCollapse,
    NCollapseItem
  },
  beforeMount() {
    const { getAscension } = heroAscentStats()
    const { getStats } = heroLimitStats()
    const { getHpMp } = heroLimitHpMp()

    var negative = require('../composables/constants/negativeEffects.json')
    var adv_order = require('../data/adv_order_id.json')

    try{
      let adv_list = window.sessionStorage.getItem("adv_list")
      if(adv_list == null){
        var data = require('../data/adv_list_DATA.json')
        this.dataAdv = data
        this.filteredData = data
        window.sessionStorage.setItem("adv_list", JSON.stringify(data));

      }else{
        this.dataAdv = JSON.parse(adv_list)
        this.filteredData = JSON.parse(adv_list)
      }

      // axios.get('http://172.16.30.62:8082/aventureiro/' + this.id_char).then(response => {
      //   if(response.statusText != "OK"){
      //     throw new Error('This adventurer does not exist!');
      //   }
      this.character = this.dataAdv[adv_order[this.id_char]]
      this.ascentStats = getAscension(this.character)
      this.limitbreakStats = getStats(this.character)
      this.limitbreakHpMp = getHpMp(this.character)
      this.negativeEffects = negative
      this.updateFalnaRank()
      this.onResize()
      // }).catch(err => {
      //   this.error = err
      //   this.errorMessage = "This adventurer does not exist"
      // })
    }
    catch (err){
      this.error = err
      this.errorMessage = "This adventurer does not exist"
    }
  },
  mounted() {
    this.$nextTick(() => {
      window.addEventListener('resize', this.onResize);
    })
  },

  beforeDestroy() { 
    window.removeEventListener('resize', this.onResize); 
  },
  methods: {
    onResize() {
      this.screenWidth = window.innerWidth
    },
    getImg(url){
      return require('../assets/adventurers/hex_adv/' + url + '.png')
    },
    getRec(url){
      return require('../assets/adventurers/h_rectangle_adv/' + url + '.png')
    },
    getModel(url){
      return require('../assets/adventurers/h_full_adv/' + url + '.png')
    },
    getFesta(url){
      return require('../assets/status/limit_festa' + url + '.png')
    },
    gotoReport(){
      this.$router.push({ name: "report_adv", params: {id: this.id_char} });
    },
    gotoEdit(){
      this.$router.push({ name: "edit_adv", params: {id: this.id_char} });
    },
    gotoChar(){
      this.$router.push({ name: "adventurer_list"});
    },
    checkEachAttack(value){
      if(value.hasOwnProperty('attack')){
        if(value.attack.hasOwnProperty('perEachNumber')){
          return true
        } 
      }
      return false
    },
    checkEachAttackRepeat(value){
      if(value.hasOwnProperty('repeat_attack')){
        if(value.repeat_attack.hasOwnProperty('perEachNumber')){
          return true
        } 
      }
      return false
    },
    goAttack(atk){
      let filter = {
        targets: atk.attack.attack_target.toLowerCase().replace(/ /g, "_"),
        speed: atk.attack.attack_velocity.toLowerCase().replace(/ /g, "_"),
        power: atk.attack.attack_power.toLowerCase().replace(/ /g, "_"),
        element_atk: atk.attack.attack_element.toLowerCase().replace(/ /g, "_"),
        type: atk.attack.attack_type.toLowerCase().replace(/ /g, "_")
      }
      this.$router.push({ name: 'filter_adv', query: filter});
    },
    goModifier(atk){
      let filter = {
        modifier: atk.attack.attack_modifier1.toLowerCase().replace(/ /g, "_")
      }
      this.$router.push({ name: 'filter_adv', query: filter});
    },
    goIndex(atk, n){
      let filter = {
        modifier: atk.attack["index_" + n].toLowerCase().replace(/ /g, "_")
      }
      this.$router.push({ name: 'filter_adv', query: filter});
    },
    goModifierRepeat(atk){
      let filter = {
        modifier: atk.repeat_attack.attack_modifier1.toLowerCase().replace(/ /g, "_"),
        filter_repeat_attack: "repeat"
      }
      this.$router.push({ name: 'filter_adv', query: filter});
    },
    goIndexRepeat(atk, n){
      let filter = {
        modifier: atk.repeat_attack["index_" + n].toLowerCase().replace(/ /g, "_"),
        filter_repeat_attack: "repeat"
      }
      this.$router.push({ name: 'filter_adv', query: filter});
    },
    goPerEach(atk, each){
      let filter = {
        pereach_ef: atk.attack["perEachEffect" + each].toLowerCase().replace(/ /g, "_"),
      }
      this.$router.push({ name: 'filter_adv', query: filter});
    },
    goPerEachRepeat(atk, each){
      let filter = {
        pereach_ef: atk.repeat_attack["perEachEffect" + each].toLowerCase().replace(/ /g, "_"),
      }
      this.$router.push({ name: 'filter_adv', query: filter});
    },
    goRepeat(atk){
      let filter = {
        n_repeat: atk.repeat_attack.repeatCount,
      }
      this.$router.push({ name: 'filter_adv', query: filter});
    },
    negativeEff(value, combat_skill){
      if(value.effects[combat_skill + "targets"] == "Allies" || value.effects[combat_skill + "targets"] == "Ally" || value.effects[combat_skill + "targets"] == "Self"){
        if(parseInt(value.effects[combat_skill + "value"].toLowerCase().replace(/ /g, "_").replace("%", "")) < 0){
          if(this.negativeEffects.allies_neg.hasOwnProperty(value.effects[combat_skill + "effect"])){
            return true
          }
        }else{
          if(this.negativeEffects.allies_pos.hasOwnProperty(value.effects[combat_skill + "effect"])){
            return true
          }
        }
      }
      return false
    },
    perEachNegative(value, eff){
      if(value.repeat_attack["perEachTarget"] == "Self"){
        if(value.repeat_attack["perEachEffect" + eff].indexOf("Debuff") > -1){
          return true
        }
        if(value.repeat_attack["perEachEffect" + eff].indexOf("Target Damage Buff") == -1){
          return true
        }
      }
      if(value.repeat_attack["perEachTarget"] == "Target"){
        if(value.repeat_attack["perEachEffect" + eff].indexOf("Target Damage Debuff") == -1){
          return true
        }
      }
      return false
    },
    goRepeatAttack(atk){
      let filter = {
        targets: atk.repeat_attack.attack_target.toLowerCase().replace(/ /g, "_"),
        speed: atk.repeat_attack.attack_velocity.toLowerCase().replace(/ /g, "_"),
        power: atk.repeat_attack.attack_power.toLowerCase().replace(/ /g, "_"),
        element_atk: atk.repeat_attack.attack_element.toLowerCase().replace(/ /g, "_"),
        type: atk.repeat_attack.attack_type.toLowerCase().replace(/ /g, "_"),
        filter_repeat_attack: "repeat"
      }
      this.$router.push({ name: 'filter_adv', query: filter});
    },
    goEffect(eff, skill){
      let filter = {
        ef_target: eff.effects[skill + "targets"].toLowerCase().replace(/ /g, "_"),
        ef: eff.effects[skill + "effect"].toLowerCase().replace(/ /g, "_"),
      }
      let val = eff.effects[skill + "value"]
      if(val){
        let tempval = parseInt(eff.effects[skill + "value"].toLowerCase().replace(/ /g, "_").replace("%", ""))
        if(tempval >= 0){
          filter["ef_val1"] = tempval
        }else{
          filter["ef_val2"] = tempval
        }
      }
      this.$router.push({ name: 'filter_adv', query: filter});
    },
    goEffectRepeat(eff, skill){
      let filter = {
        ef_target: eff.repeat_attack.effects[skill + "targets"].toLowerCase().replace(/ /g, "_"),
        ef: eff.repeat_attack.effects[skill + "effect"].toLowerCase().replace(/ /g, "_"),
        filter_repeat_effect: "repeat"
      }
      let val = eff.repeat_attack.effects[skill + "value"]
      if(val){
        let tempval = parseInt(eff.repeat_attack.effects[skill + "value"].toLowerCase().replace(/ /g, "_").replace("%", ""))
        if(tempval >= 0){
          filter["ef_val1"] = tempval
        }else{
          filter["ef_val2"] = tempval
        }
      }
      if(isRepeat){
        filter["filter_repeat_effect"] = "repeat"
      }
      this.$router.push({ name: 'filter_adv', query: filter});
    },
    goCombination(combination_skill){
      let skill = combination_skill.toLowerCase().replace(/ /g, "_")
      this.$router.push({ name: 'filter_adv', query: { comb_skill: skill }});
    },
    goDevelopmentName(skill){
      let filter = {
        devname: skill["name"].toLowerCase().replace(/ /g, "_"),
      }
      this.$router.push({ name: "filter_adv", query: filter});
    },
    goDevelopment(skill, dev){
      let filter = {
        devskill: skill["effect_" + dev].toLowerCase().replace(/ /g, "_")
      }
      if(skill["value_" + dev]){
        let tempeff = parseInt(skill["value_" + dev].toLowerCase().replace(/ /g, "_").replace("%", ""))
        if(tempeff >= 0){
          filter["dev_valmin"] = tempeff
        }else{
          filter["dev_valmax"] = tempeff
        }
      }
      this.$router.push({ name: "filter_adv", query: filter});
    },
    colapseCombat(keys){
      this.combat_dropdown[keys] = !this.combat_dropdown[keys]
    },
    swapAttack(index){
      let skill = ""
      switch (index) {
        case 0:
          skill = "special"
          break;
        case 1:
          skill = "skill1"
          break;
        case 2:
          skill = "skill2"
          break;
        case 3:
          skill = "skill3"
          break;
        default:
          skill = "special"
          break;
      }
      let temp = this.character.combat_skills[skill].raw_text
      this.character.combat_skills[skill].raw_text = this.character.combat_skills[skill].raw_festa
      this.character.combat_skills[skill].raw_festa = temp

      temp = this.character.combat_skills[skill].attack
      this.character.combat_skills[skill].attack = this.character.combat_skills[skill].attack_festa
      this.character.combat_skills[skill].attack_festa = temp

      temp = this.character.combat_skills[skill].effects
      this.character.combat_skills[skill].effects = this.character.combat_skills[skill].effects_festa
      this.character.combat_skills[skill].effects_festa = temp

      temp = this.character.combat_skills[skill].repeat_attack
      this.character.combat_skills[skill].repeat_attack = this.character.combat_skills[skill].repeat_attack_festa
      this.character.combat_skills[skill].repeat_attack_festa = temp

      this.festaSkill[index] = !this.festaSkill[index]
    },
    decideBase(star, hero_asc, hero_festa){
      if(!false){
        switch (star) {
          case 1:
            return require('../assets/frame/frame_1.png')
          case 2:
            return require('../assets/frame/frame_2.png')
          case 3:
            return require('../assets/frame/frame_3.png')  
          case 4:
            if(hero_festa){
              return require('../assets/frame/frame_4_festa.png')
            }
            if(!hero_asc){return require('../assets/frame/frame_4.png')}
            else{return require('../assets/frame/frame_4_star.png')}
          default:
            return require('../assets/frame/frame_1.png')    
        }
      }
    },
    decideBaseFrame(star, hero_asc, hero_festa){
      if(!false){
        switch (star) {
          case 1:
            return require('../assets/frame/base_frame_1.png')
          case 2:
            return require('../assets/frame/base_frame_2.png')
          case 3:
            return require('../assets/frame/base_frame_3.png')  
          case 4:
            if(hero_festa){
              return require('../assets/frame/frame_4_festa.png')
            }
            if(!hero_asc){return require('../assets/frame/base_frame_4.png')}
            else{return require('../assets/frame/base_frame_4_star.png')}
          default:
            return require('../assets/frame/base_frame_1.png')    
        }
      }
    },
    decideType(type_attack){
      switch(type_attack){
        case "physical_type":
          return require('../assets/attacktype/physical.png')
        case "phys_plus":
          return require('../assets/attacktype/physical.png')
        case "phys_festa":
          return require('../assets/attacktype/physical.png')
        case "magic_type":
          return require('../assets/attacktype/magic.png')
        case "mag_plus":
          return require('../assets/attacktype/magic.png')
        case "balance_type":
          return require('../assets/attacktype/balanced.png')
        case "bala_phys":
          return require('../assets/attacktype/balanced.png')
        case "bala_mag":
          return require('../assets/attacktype/balanced.png')
        case "bala_plus_phys":
          return require('../assets/attacktype/balanced.png')
        case "bala_plus_mag":
          return require('../assets/attacktype/balanced.png')
        case "defense_type":
          return require('../assets/attacktype/defender.png')        
        case "healer_type":
          return require('../assets/attacktype/healer.png')
        default:
          return require('../assets/attacktype/physical.png')
      }
    },
    decideTypePhys(type_attack){
      switch(type_attack){
        case "physical_type":
          return 'Physical Type'
        case "phys_plus":
          return 'Physical Type'
        case "phys_festa":
          return 'Physical Festa'
        case "magic_type":
          return 'Magic Type'
        case "mag_plus":
          return 'Magic Type'
        case "balance_type":
          return 'Balanced Type'
        case "bala_phys":
          return 'Balanced Type'
        case "bala_mag":
          return 'Balanced Type'
        case "bala_plus_phys":
          return 'Balanced Type'
        case "bala_plus_mag":
          return 'Balanced Type'
        case "defense_type":
          return 'Defense Type'
        case "healer_type":
          return 'Healer Type'
        default:
          return 'Physical Type'
      }
    },
    decideSkillsLabel(attack){
      switch(attack){
        case "special":
          return 'Special'
        case "skill1":
          return 'Skill 1'
        case "skill2":
          return 'Skill 2'
        case "skill3":
          return 'Skill 3'
        default:
          return 'Skill'
      }
    },
    decideTrueFalse(boolea){
      switch(boolea){
        case true:
          return 'Yes'
        case false:
          return 'No'
        default:
          return 'No'
      }
    },
    decideElementLabel(element){
      switch(element){
        case "fire":
          return 'Fire'
        case "water":
          return 'Water'
        case "earth":
          return 'Earth'
        case "thunder":
          return 'Thunder'
        case "wind":
          return 'Wind'
        case "dark":
          return 'Dark'
        case "light":
          return 'Light'
        case "none":
          return 'None'
        default:
          return 'None'
      }
    },
    decideTypeKiller(attack){
      switch(attack){
        case "dragon_killer":
          return 'Dragon Killer'
        case "dragon_slayer":
          return 'Dragon Killer'
        case "spirit_killer":
          return 'Spirit Killer'
        case "giant_killer":
          return 'Giant Killer'
        case "ox_slayer":
          return 'Ox Slayer'
        case "beast_killer":
          return 'Beast Killer'
        case "ogre_killer":
          return 'Ogre Killer'
        case "insect_killer":
          return 'Insect Killer'
        case "plant_killer":
          return 'Plant Killer'
        case "aqua_killer":
          return 'Aqua Killer'
        case "material_killer":
          return 'Material Killer'
        case "worm_killer":
          return 'Worm Killer'
        case "fantasma_killer":
          return 'Fantasma Killer'
        case "ghost_killer":
          return 'Fantasma Killer'
        case "none":
          return 'None'
        default:
          return 'None'
      }
    },

    decideAttackElement(element){
      if(element.attack.attack_element){
        return require('../assets/elements/element_attack_' + element.attack.attack_element.toLowerCase() + '.png')
      }else{
        return require('../assets/elements/element_none.png')
      }
    },
    decideElement(element){
      return require('../assets/elements/element_' + element + '.png')
    },
    // decideLimit(){
    //   if (this.limitbreak >= 0 && this.limitbreak <= 5) {
    //     return require('../assets/status/limit' + this.limitbreak + '.png')
    //   }
    // },
    // decideLimitIcon(){
    //   if (this.limitbreak >= 0 && this.limitbreak <= 5) {
    //     return require('../assets/status/limitbreak_' + this.limitbreak + '.png')
    //   }
    // },
    // decideAscent(){
    //   if (this.ascent >= 0 && this.ascent <= 6) {
    //     return require('../assets/status/star' + this.ascent + '.png')
    //   }
    // },
    changeLimit(add){
      let res = this.limitbreak + add
      if ((res >= 0 && res <= 5)){
        let change = this.limitbreak
        this.limitbreak += add
        if(add >= 1){
          this.limitbreakValue[0] += this.limitbreakStats.str[change]
          this.limitbreakValue[1] += this.limitbreakStats.end[change]
          this.limitbreakValue[2] += this.limitbreakStats.dex[change]
          this.limitbreakValue[3] += this.limitbreakStats.agi[change]
          this.limitbreakValue[4] += this.limitbreakStats.mag[change]
          this.limitbreakValueHpMp[0] += this.limitbreakHpMp.hp[change]
          this.limitbreakValueHpMp[1] += this.limitbreakHpMp.mp[change]
        }else{
          change -= 1
          this.limitbreakValue[0] -= this.limitbreakStats.str[change]
          this.limitbreakValue[1] -= this.limitbreakStats.end[change]
          this.limitbreakValue[2] -= this.limitbreakStats.dex[change]
          this.limitbreakValue[3] -= this.limitbreakStats.agi[change]
          this.limitbreakValue[4] -= this.limitbreakStats.mag[change]
          this.limitbreakValueHpMp[0] -= this.limitbreakHpMp.hp[change]
          this.limitbreakValueHpMp[1] -= this.limitbreakHpMp.mp[change]
        }
        this.updateFalnaRank()
      }
    },
    setLimit(setLimit){
      let tempStats = [0, 0, 0, 0, 0, 0, 0]
      if(setLimit != 0){
        for (let i = 0; i < setLimit; i++){
          tempStats[0] += this.limitbreakStats.str[i]
          tempStats[1] += this.limitbreakStats.end[i]
          tempStats[2] += this.limitbreakStats.dex[i]
          tempStats[3] += this.limitbreakStats.agi[i]
          tempStats[4] += this.limitbreakStats.mag[i]
          tempStats[5] += this.limitbreakHpMp.hp[i]
          tempStats[6] += this.limitbreakHpMp.mp[i]
        }
      }
      // console.log(tempStats)
      tempStats.forEach((stat, index) => {
        if(index <= 4){
          this.limitbreakValue[index] = stat
        }else{
          this.limitbreakValueHpMp[index - 5] = stat
        }
      });
      this.limitbreak = setLimit
      this.updateFalnaRank()
    },
    clickLimit(e){
      let limitX = Math.round(e.clientX - this.$refs.limitLevel.getBoundingClientRect().left)
      if (limitX < 6){
        this.setLimit(0)
      }
      else if (limitX >= 6 && limitX < 32){
        this.setLimit(1)
      }
      else if (limitX >= 32 && limitX < 58){
        this.setLimit(2)
      }
      else if (limitX >= 58 && limitX < 84){
        this.setLimit(3)
      }
      else if (limitX >= 84 && limitX < 110){
        this.setLimit(4)
      }
      else{
        this.setLimit(5)
      }
    },  
    changeMaxMin(ifmin){
      if(ifmin == true){
        this.setAscent(0)
        this.setLimit(0)
      }else{
        this.setAscent(6)
        this.setLimit(5)
      }
    },
    changeAscent(add){
      let res = this.ascent + add

      if ((res >= 0 && res <= 6)){
        let change = this.ascent
        this.ascent += add
        if(add >= 1){
          this.ascentValue[0] += this.ascentStats.str[change]// STR
          this.ascentValue[1] += this.ascentStats.end[change]// END
          this.ascentValue[2] += this.ascentStats.dex[change]// DEX
          this.ascentValue[3] += this.ascentStats.agi[change]// AGI
          this.ascentValue[4] += this.ascentStats.mag[change]// MAG
          this.ascentValue[5] += this.ascentStats.hp[change] // HP
          this.ascentValue[6] += this.ascentStats.mp[change] // MP
        }else{
          change -= 1
          this.ascentValue[0] -= this.ascentStats.str[change]// STR
          this.ascentValue[1] -= this.ascentStats.end[change]// END
          this.ascentValue[2] -= this.ascentStats.dex[change]// DEX
          this.ascentValue[3] -= this.ascentStats.agi[change]// AGI
          this.ascentValue[4] -= this.ascentStats.mag[change]// MAG
          this.ascentValue[5] -= this.ascentStats.hp[change] // HP
          this.ascentValue[6] -= this.ascentStats.mp[change] // MP
        }
        this.updateFalnaRank()
      }
    },
    setAscent(setStar){
      let tempStats = [0, 0, 0, 0, 0, 0, 0]
      if(setStar != 0){
        for (let i = 0; i < setStar; i++){
          tempStats[0] += this.ascentStats.str[i]// STR
          tempStats[1] += this.ascentStats.end[i]// END
          tempStats[2] += this.ascentStats.dex[i]// DEX
          tempStats[3] += this.ascentStats.agi[i]// AGI
          tempStats[4] += this.ascentStats.mag[i]// MAG
          tempStats[5] += this.ascentStats.hp[i] // HP
          tempStats[6] += this.ascentStats.mp[i] // MP
        }
      }
      tempStats.forEach((stat, index) => {
        this.ascentValue[index] = stat
      });
      this.ascent = setStar
      this.updateFalnaRank()
    },
    clickAscent(e){
      let starsX = Math.round(e.clientX - this.$refs.ascentStars.getBoundingClientRect().left)
      if (starsX < 15){
        this.setAscent(0)
      }
      else if (starsX >= 15 && starsX < 36){
        this.setAscent(1)
      }
      else if (starsX >= 36 && starsX < 59){
        this.setAscent(2)
      }
      else if (starsX >= 59 && starsX < 78){
        this.setAscent(3)
      }
      else if (starsX >= 78 && starsX < 97){
        this.setAscent(4)
      }
      else if (starsX >= 97 && starsX < 116){
        this.setAscent(5)
      }
      else{
        this.setAscent(6)
      }
    },  
    updateFalnaRank(){
      const { getAdvRank } = numberToRank()
      const { calculateStats } = heroLimitStats()

      var newLimit = calculateStats(this.limitbreakValue, this.character.dev_abilities)
      this.limitbreakDisplay = newLimit;
      // console.log(this.ascentValue)
      // console.log(newLimit)
      this.falnaImages = getAdvRank([
        this.character.str + this.ascentValue[0] + newLimit[0],
        this.character.end + this.ascentValue[1] + newLimit[1],
        this.character.dex + this.ascentValue[2] + newLimit[2],
        this.character.agi + this.ascentValue[3] + newLimit[3],
        this.character.mag + this.ascentValue[4] + newLimit[4]
      ])
    },
    getFalnaImages(falna){
      return require('../assets/rank/' + falna + '.png')
    },
  },

}
</script>

